<!--  -->
<template>
  
  <!--  -->
  <el-menu
    :default-active="activeIndex"
    class="el-menu-demo"
    :class="{ 'dark-theme': currentTheme === 'dark' }"
    mode="horizontal">


    <!--  -->
    <el-menu-item index="home" @click="handleHome" class="home-menu-item">
      <el-icon><House /></el-icon>
    </el-menu-item>
    <!--  -->
    <!--<el-menu-item index="about" @click="$emit('show-about')" v-show="true">-->
     <!-- <el-icon><InfoFilled /></el-icon>-->
    <!--</el-menu-item>-->
    
    <!--  -->
    <el-menu-item index="donate" @click="$emit('show-donate')" title="">
      <el-icon><Coffee /></el-icon>
    </el-menu-item>

    <!--  -->
    <el-menu-item index="reward" @click="$emit('show-reward')" v-show="false">
      <el-icon><Plus /></el-icon>
    </el-menu-item>

    <el-menu-item index="theme" @click="$emit('toggle-theme')">
      <el-icon>
        <Sunny v-if="currentTheme === 'dark'" />
        <Moon v-else />
      </el-icon>
    </el-menu-item>

    <!--  -->
    <el-menu-item
    index="search"
    class="search-item"
    style="margin-left: auto; position: relative; z-index: 3000;"
    @click="handleMenuClick"
    @mousedown="handleMenuMouseDown"
    @touchstart="handleMenuTouchStart"
  >
      <div class="search-container" @click.stop @mousedown.stop @touchstart.stop>
        <!--  -->
        <el-select
          v-model="selectedEngine"
          class="search-engine-select"
          :teleported="false"
          :style="[
            
            { width: '300px' }
          ]"
        >
          <template #prefix>
            <el-icon class="search-engine-icon">
              <component :is="currentEngineIcon" />
            </el-icon>
          </template>
          <el-option
            v-for="engine in searchEngines"
            :key="engine.value"
            :label="engine.label"
            :value="engine.value"
            :class="{ 'dark-theme': currentTheme === 'dark' }"
            class="search-engine-option"
          >
            <div class="search-engine-option-content">
              <el-icon class="search-engine-icon">
                <component :is="engine.icon" />
              </el-icon>
              <span>{{ engine.label }}</span>
            </div>
          </el-option>
        </el-select>
        <!--  -->
        <el-input
          ref="searchInput"
          v-model="searchText"
          @keyup.enter="handleSearch"
          @input="handleSearchInput"
          class="search-input"
          @keydown="handleKeyDown"
          placeholder=""
        >
        

          <template #append>
            <el-button @click="handleSearch">
              <el-icon><Search /></el-icon>
            </el-button>
            <!--  -->
            <el-button 
              @click="clearSearch" 
              :class="{ 'dark-theme': currentTheme === 'dark' }"
              v-if="searchText"
              class="clear-button"
            >
              <el-icon><Close /></el-icon>
            </el-button>
          </template>
        </el-input>

      </div>
    </el-menu-item>
  </el-menu>
</template>

<script>
import { House, InfoFilled, Coffee, Plus, Moon, Sunny, Search, Close, ChromeFilled, Picture, VideoPlay, Menu } from '@element-plus/icons-vue'
//   ESLint 
// import { getPinyinInitials } from "../utils/pinyin.js"

export default {
  name: 'NavigationHeader',
  components: {
    House, InfoFilled, Coffee, Plus, Moon, Sunny, Search, Close, ChromeFilled, Picture, VideoPlay, Menu
  },
  props: {
    activeIndex: { type: String, default: '' },
    currentTheme: { type: String, required: true },
    categoriesData: { type: Array, default: () => [] }
  },
  emits: ['show-about', 'show-donate', 'show-reward', 'select', 'toggle-theme', 'search-input'],
  data() {
    return {
      filteredCategories: [],
      searchText: '',
      selectedEngine: 'google',
      searchEngines: [
        { label: 'Google', value: 'google', url: 'https://www.google.com/search?q=%s', icon: 'ChromeFilled' },
        { label: 'Bing', value: 'bing', url: 'https://www.bing.com/search?q=%s', icon: 'Search' },
        { label: '', value: 'baidu', url: 'https://www.baidu.com/s?wd=%s', icon: 'Search' },
        { label: 'B', value: 'bilibili', url: 'https://www.bilibili.com/search?keyword=%s', icon: 'VideoPlay' },
        { label: 'GitHub', value: 'github', url: 'https://github.com/search?q=%s', icon: 'Search' },
        { label: 'Youtube', value: 'youtube', url: 'https://www.youtube.com/results?search_query=%s', icon: 'VideoPlay' },
      ],
      isMobile: false,
      resizeTimer: null
    }
  },
  computed: {
    currentEngineIcon() {
      return this.searchEngines.find(engine => engine.value === this.selectedEngine)?.icon || 'Search'
    }
  },
  mounted() {
    this.checkIfMobile();
    window.addEventListener('resize', this.handleResize);
    window.addEventListener('keydown', this.handleKeydown);
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.handleResize);
    window.removeEventListener('keydown', this.handleKeydown);
  },
  methods: {
    handleResize() {
      if (this.resizeTimer) clearTimeout(this.resizeTimer);
      this.resizeTimer = setTimeout(() => {
        this.checkIfMobile();
      }, 100);
    },
    checkIfMobile() {
      this.isMobile = window.innerWidth <= 768;
    },

    handleKeydown(event) {
      const input = this.$refs.searchInput?.input;
      if (!input || document.activeElement !== input) return;
      if (event.isComposing) return;

      if (event.code === 'Space' || event.key === ' ') {
         // 
      }
    },

    handleHome() {
      window.location.href = '/'; 
    },

    handleSearch() {
      if (!this.searchText) return;
      const searchUrl = this.searchEngines.find(engine => engine.value === this.selectedEngine)?.url;
      if (searchUrl) {
        window.open(searchUrl.replace('%s', encodeURIComponent(this.searchText)), '_blank');
      }
    },

    handleSearchInput(value) {
      this.$emit('search-input', value);
    },

    clearSearch() {
      this.searchText = '';
      this.$emit('search-input', '');
      this.$nextTick(() => {
        this.$refs.searchInput?.focus();
      });
    }
  }
}
</script>

